import Navigator from "../components/navigators/Navigator";
import {mapState} from "vuex";

export default {
  name: "default",
  components: {Navigator},
  computed: {
    style: function () {
      return {
        'background-color': this.backgroundColor,
        'transition': 'background-color 200ms'
      }
    },
    ...mapState({
      backgroundColor: 'backgroundColor'
    })
  }
}
